/* 바디 */
body {
  width: 100%;
  margin: 0;
  font-family: 'nanumsquare';
}
/* 탑바 */
.top-bar{
  background-color: black;
  /* width: 100%; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
  padding: 20px;
  padding-left: 100px;
  font-weight: 500;
  font-size: 30px;
}

.top-bar-item1{
  width: 18%;
  border-right: solid 1px white;
}

.top-bar-item2{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 25%;
  background-color: black;
  border-left: solid 1px white;

}


.top-bar-item2-img{
  background-size: contain;
  object-fit: fill;
  width: 10px;
  height: 10px;
}




/* 탑바 밑부분 내비게이터 */
.top-bar-bottom{
  background-color: rgb(78, 75, 75);
  color: white;
  display: flex;
  flex-direction: row;
  /* padding: 20px;  */
}

.top-bar-bottom-left{
  width: 23%;
}

.top-bar-bottom-right{
  display: flex;
  width: 70%;
  
}
.button-box{
  border-right: solid 1px white;
  display: flex;
  justify-content: center;
  align-items: center;  


}
.top-bar-bottom-item{
  font-size: 18px;
  margin: 20px;

}

/* 컨텐츠 (왼쪽사이드바,센터컨텐츠,오른쪽사이드바)*/
.content-box{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* 왼쪽사이드바*/
.left-side-bar{
  background-color: rgb(82, 68, 68);
  width: 17%;
  display: flex;
  color: white;
  flex-direction: column;
  padding: 20px;
  height: 100vh;
  border-top: solid 1px white;
}
.left-side-bar-item{
  margin: 10px;
  font-size: 15px;
  font-weight: 400;
}
/* 가운데 컨텐츠*/
.center-content{
  width: 70%;
  padding: 30px;
}

/* 지피티 게시판*/
/* App.css */
.post-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
}

.post-content {
  margin: 20px;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/

.list-table{
  width: 100%;
}

/* 게시글 목록*/
.list-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
.list-table th, .list-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}
.list-table th {
  background-color: #F2F2F2;
}
.list-table tbody tr:hover {
  background-color: #F5F5F5;
  cursor: pointer;
}
.list-table button {
  background-color: #FF6347;
  color: white;
  border: none;
  padding: 6px 10px;
  cursor: pointer;
}


/*등록폼*/
.enroll-table{
  width: 100%;
  margin-top: 20px;
}
.enroll-table th, .enroll-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
.enroll-table input, .enroll-table textarea {
  width: 100%;
  padding: 6px;
  box-sizing: border-box;
}
.enroll-table button {
  background-color: #212957;
  color: white;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
}
.enroll-table button:hover {
  background-color: #333c70;
}

/* 게시글 상세보기 */
.detail-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
.detail-table th, .detail-table td {
  border: 1px solid #ddd;
  padding: 8px;
}
.detail-table th {
  background-color: #F2F2F2;
}
.detail-table td[colSpan="3"] {
  height: 200px;
}
.btn-area {
  margin-top: 20px;
}
.btn-area a {
  text-decoration: none;
  background-color: #4CAF50;
  color: white;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}
.btn-area a:hover {
  background-color: #45A049;
}



/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
/* 오른쪽 사이드바*/
.right-side-bar{
  background-color: #212957;
  color: white;
  display: flex;
  flex-direction: column;
  width: 13%;
  margin: 0;
  padding: 0;
}
.right-side-bar-item{
  border: solid 1px white;
  height: 30%;

}
.right-side-bar-item img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}


/*푸터*/
.footer-box{
  background-color: rgb(209, 209, 209);
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 110px;
  padding: 30px;  
 
}

.footer-item1{
  border: solid 1px black;
  border-top: none;
  border-bottom: none;
  width: 30%;
}
.footer-item2{
  border: solid 1px black;
  border-top: none;
  border-bottom: none;
  width: 30%;
}
.footer-item3{
  border: solid 1px black;
  border-top: none;
  border-bottom: none;
  width: 30%;
}







